import React from 'react'
import classname from 'classname'

import './Select.scss'

class Select extends React.Component {
  static defaultProps = {
    data: []
  }

  constructor(props){
    super(props)
  }

  renderData(data) {
    return data.map((item, i) => (
        <option
          key={i}
          value={item.value}
          {...item}>
          {item.label}
       </option>
    ))
  }

  render () {
    const { className, data, children, ...others } = this.props
    let selectClass = classname('select', className)
    return (
      <select className={selectClass} {...others}>
        {data.length > 0 ? this.renderData(data) : children}
      </select>
    )
  }
}

export default Select