import React from 'react'

import SideBar from '../SideBar/SideBar'
import logo from '../../assets/img/aura.png'
import './TopBar.scss'

class TopBar extends React.Component {
  static defaultProps = {
      title: 'PMS',
      pathname: '/',
  }

  constructor(props){
    super(props)

    this.state = {
      show: false
    } 

    this.toggleSidebar = this.toggleSidebar.bind(this)
  }

  toggleSidebar () {
    this.setState({
      show: !this.state.show
    })
  }

  render () {
    return (
      <div>
        <div className='topbar' onClick={this.toggleSidebar}>
          <div className='topbar_title'>
            {this.props.title}
          </div>
          <div className="topbar_logo">
            <img src={logo} className="logo_rotation" />
          </div>
        </div>

        <SideBar show={this.state.show} hideSidebar={this.toggleSidebar} {...this.props}/>
      </div>
    )
  }
}

export default TopBar