import { createReducers } from '../modules/redux-util'

let DiaryAddApp = createReducers({
  getInitialState () {
    return {
      status: '',
    }
  },
  addDiary (state, action) {
    state.status = ''
    return state
  },
  addDiaryPedding (state, action) {
    state.status = 'pending'
    return state
  },
  addDiarySuccess (state, action) {
    state.status = 'success'
    return state
  },
  addDiaryError (state, action) {
    state.status = 'error'
    return state
  },
})

export default DiaryAddApp