import React, { Component } from 'react'
import { connect } from 'react-redux'
import * as Actions from '../../actions/consumption'
const mapStateToProps = state => state
const mapDispatchToProps = Actions

import Axios from '../../modules/Axios'
import './Pedometer.scss'

class Pedometer extends Component {

  constructor(props) {
      super(props)

      this.state = {
      }

      this.onTestGet = this.onTestGet.bind(this)
      this.onTestPost = this.onTestPost.bind(this)
  }

  onGetWxSession () {
    Axios.get('/wx/get-wx-session', {code: '003DxM280UnjGI1XaV180OpJ280DxM2D'})
      .then((data)=>{
        console.log(data)
      })
  }

  onDecryptData () {
    let iv = "weJjQXrD99jhPn+ttcmUDw=="
    let encryptedData = ""
    Axios.post('/wx/decrypt-data', {encryptedData: encryptedData, iv: iv})
      .then((data)=>{
        console.log(data)
      })
  }

  onTestGet () {
    Axios.get('/pedometer/index', {})
      .then((data)=>{
        console.log(data)
      })
  }

  onTestPost () {
    let testParams = {
      stepInfoList: [
        {
            "timestamp": 1519629269,
            "step": 10086
        },
        {
            "timestamp": 1445866602,
            "step": 100
        }
      ]
    }

    Axios.post('/pedometer/update', testParams)
      .then((data)=>{
        console.log(data)
      })
  }

  render() {
    return (
      <div className="Pedometer">
        Pedometer

        <div onClick={this.onGetWxSession}>
          <br/><br/>
          获取SessionKsy
          <br/><br/>
        </div>
        <div onClick={this.onDecryptData}>
          <br/><br/>
          解密文件
          <br/><br/>
        </div>

        <div onClick={this.onTestGet}>
          <br/><br/>
          获取数据
          <br/><br/>
        </div>
        <div onClick={this.onTestPost}>
          <br/><br/>
          写入数据
          <br/><br/>
        </div>
      </div>
    )
  }
}

export default connect(
  mapStateToProps, 
  mapDispatchToProps
)(Pedometer)