import { createReducers } from '../modules/redux-util'

let ConsumptionUpdateApp = createReducers({
  getInitialState () {
    return {
      updateStatus: '',
      deleteStatus: '',
      current: {
        uConsumptionID: 0,
        sConsumptionItem: '',
        dMoney: '',
        sDate: '',
        Category: 'food',
        sRemark: '',
      }
    }
  },
  setCurrentConsumption (state, action) {
    state.current = action.payload
    return state
  }, 
  updateConsumption (state, action) {
    state.updateStatus = ''
    return state
  },
  updateConsumptionPedding (state, action) {
    state.updateStatus = 'pending'
    return state
  },
  updateConsumptionSuccess (state, action) {
    state.updateStatus = 'success'
    return state
  },
  updateConsumptionError (state, action) {
    state.updateStatus = 'error'
    return state
  },
  deleteConsumption (state, action) {
    state.deleteStatus = ''
    return state
  },
  deleteConsumptionPedding (state, action) {
    state.deleteStatus = 'pending'
    return state
  },
  deleteConsumptionSuccess (state, action) {
    state.deleteStatus = 'success'
    return state
  },
  deleteConsumptionError (state, action) {
    state.deleteStatus = 'error'
    return state
  },
})

export default ConsumptionUpdateApp