import { createAction } from '../modules/redux-util'
import Immutable from 'immutable'
import Axios from '../modules/Axios'

export let initDiaryPage = createAction('initDiaryPage')
export let nextDiaryPage = createAction('nextDiaryPage')
export let setCurrentDiary = createAction('setCurrentDiary')

export let getDiaryPedding = createAction('getDiaryPedding')
export let getDiarySuccess = createAction('getDiarySuccess')
export let getDiaryError = createAction('getDiaryError')
export let getDiary = createAction('getDiary', (callback) => (dispatch, getState) => {
  dispatch( getDiaryPedding() )

  let currentPage = getState().DiaryApp.currentPage
  let reqParams = {
    page: currentPage
  }

  Axios.get('/diary/index', reqParams)
    .then((data)=>{
      if (data.Ret == '0') {
        dispatch( getDiarySuccess(data) )
        if (callback) callback(data)
      } else {
        dispatch( getDiaryError() )
      }
    })
    .catch((error)=>{
      console.error(error)
      dispatch( getDiaryError() )
    });
})