import { createReducers } from '../modules/redux-util'

let NoteUpdateApp = createReducers({
  getInitialState () {
    return {
      updateStatus: '',
      deleteStatus: '',
      current: {
        uNoteID: 0,
        sNoteTitle: '',
        tNoteContent: '',
      }
    }
  },
  setCurrentNote (state, action) {
    state.current = action.payload
    return state
  }, 
  updateNote (state, action) {
    state.updateStatus = ''
    return state
  },
  updateNotePedding (state, action) {
    state.updateStatus = 'pending'
    return state
  },
  updateNoteSuccess (state, action) {
    state.updateStatus = 'success'
    return state
  },
  updateNoteError (state, action) {
    state.updateStatus = 'error'
    return state
  },
  deleteNote (state, action) {
    state.deleteStatus = ''
    return state
  },
  deleteNotePedding (state, action) {
    state.deleteStatus = 'pending'
    return state
  },
  deleteNoteSuccess (state, action) {
    state.deleteStatus = 'success'
    return state
  },
  deleteNoteError (state, action) {
    state.deleteStatus = 'error'
    return state
  },
})

export default NoteUpdateApp