import { createReducers } from '../modules/redux-util'

let NoteAddApp = createReducers({
  getInitialState () {
    return {
      status: '',
    }
  },
  addNote (state, action) {
    state.status = ''
    return state
  },
  addNotePedding (state, action) {
    state.status = 'pending'
    return state
  },
  addNoteSuccess (state, action) {
    state.status = 'success'
    return state
  },
  addNoteError (state, action) {
    state.status = 'error'
    return state
  },
})

export default NoteAddApp