import React from 'react'
import classname from 'classname'

import './Loading.scss'

class Loading extends React.Component {
  static defaultProps = {
      show: true,
      className: '',
  }

  constructor(props){
    super(props)
  }

  render () {
    let className = classname('load', this.props.className)
    if (this.props.show) {
      return (
        <div className={className}>
          <i></i>
        </div>
      ) 
    } else {
      return null
    }
  }
}

export default Loading