import React, { Component } from 'react'
import { connect } from 'react-redux'
import * as Actions from '../../actions/noteAdd'
const mapStateToProps = state => state
const mapDispatchToProps = Actions
import classname from 'classname'

import Input from '../../components/Input/Input'
import Textarea from '../../components/Textarea/Textarea'
import Button from '../../components/Button/Button'
import TopTips from '../../components/TopTips/TopTips'
import './NoteAdd.scss'

class NoteAdd extends Component {

  constructor(props) {
      super(props)

      this.state = {
        sNoteTitle: '',
        tNoteContent: '',
      }

      this.onChange = this.onChange.bind(this)
      this.onSubmit = this.onSubmit.bind(this)
      this.renderBtn = this.renderBtn.bind(this)
  }

  onChange (key, e) {
    let val = e.target.value

    this.setState({
      [key]: val
    })
  }

  onSubmit () {
    this.props.addNote({
      sNoteTitle: this.state.sNoteTitle,
      tNoteContent: this.state.tNoteContent,
    }, ()=>{
      // 回到列表
      this.props.showToptips({msg: '已新增'})
      this.props.history.push( 'note' )
    })
  }

  renderBtn () {
    if (this.props.NoteAddApp.status === 'pending') {
      return (
        <Button type='btn-add' disabled="disabled">
          新增中...
        </Button>
      )
    } else {
      return (
        <Button type='btn-add' onClick={this.onSubmit}>
          新增
        </Button>
      )
    }
  }

  render () {
    let state = this.state

    return (
      <div className="Note_add">
        <TopTips/>

        <div className="note_form">
          <Input type="text" value={state.sNoteTitle} onChange={this.onChange.bind(this, 'sNoteTitle')} />
          <Textarea value={state.tNoteContent} onChange={this.onChange.bind(this, 'tNoteContent')} />
          
          {this.renderBtn()}
        </div>
      </div>
    )
  }
}

export default connect(
  mapStateToProps, 
  mapDispatchToProps
)(NoteAdd)