import React from 'react'
import classname from 'classname'

import './Input.scss'

class Input extends React.Component {

  constructor(props){
    super(props)
  }

  render () {
    const { className, inputClassName, ...others } = this.props;
    let wrapperClass = classname('input_wrapper', className)
    let inputClass = classname('input', inputClassName)
    return (
      <div className={wrapperClass}>
        <input className={inputClass} {...others} />
      </div>
    )
  }
}

export default Input