import React, { Component } from 'react'
import { connect } from 'react-redux'
import * as Actions from '../../actions/diaryUpdate'
const mapStateToProps = state => state
const mapDispatchToProps = Actions
import classname from 'classname'

import Time from '../../modules/Util/Time'
import Input from '../../components/Input/Input'
import Textarea from '../../components/Textarea/Textarea'
import Button from '../../components/Button/Button'
import TopTips from '../../components/TopTips/TopTips'
import './DiaryUpdate.scss'

class DiaryUpdate extends Component {

  constructor(props) {
      super(props)
      this.state = {
        uDiaryID: this.props.DiaryUpdateApp.current.uDiaryID,
        sDate: this.props.DiaryUpdateApp.current.sDate,
        tDiaryContent: this.props.DiaryUpdateApp.current.tDiaryContent,
      }

      this.onChange = this.onChange.bind(this)
      this.onSubmit = this.onSubmit.bind(this)
      this.onDelete = this.onDelete.bind(this)
      this.renderUpdateBtn = this.renderUpdateBtn.bind(this)
      this.renderDeleteBtn = this.renderDeleteBtn.bind(this)
  }

  onChange (key, e) {
    let val = e.target.value

    this.setState({
      [key]: val
    })
  }

  onSubmit () {
    this.props.updateDiary({
      uDiaryID: this.state.uDiaryID,
      sDate: this.state.sDate,
      tDiaryContent: this.state.tDiaryContent,
    }, ()=>{
      // 回到列表
      this.props.showToptips({msg: '已修改'})
      this.props.history.push( 'diary' )
    })
  }

  onDelete () {
    if( confirm("确认删除吗") ){
      this.props.deleteDiary({
        uDiaryID: this.state.uDiaryID,
      }, ()=>{
        // 回到列表
        this.props.showToptips({msg: '已删除'})
        this.props.history.push( 'diary' )
      })
    }
  }

  renderUpdateBtn () {
    if (this.props.DiaryUpdateApp.updateStatus === 'pending') {
      return (
        <Button type='btn-add' className="diary_btn" disabled="disabled">
          修改中...
        </Button>
      )
    } else {
      return (
        <Button type='btn-add' className="diary_btn" onClick={this.onSubmit}>
          修改
        </Button>
      )
    }
  }
  renderDeleteBtn () {
    if (this.props.DiaryUpdateApp.deleteStatus === 'pending') {
      return (
        <Button type='btn-delete' className="diary_btn" disabled="disabled">
          删除中...
        </Button>
      )
    } else {
      return (
        <Button type='btn-delete' className="diary_btn" onClick={this.onDelete}>
          删除
        </Button>
      )
    }
  }

  render () {
    let state = this.state

    return (
      <div className="Diary_add">
        <TopTips/>

        <div className="diary_form">
          <Input type="date" value={state.sDate} onChange={this.onChange.bind(this, 'sDate')} />
          <Textarea value={state.tDiaryContent} onChange={this.onChange.bind(this, 'tDiaryContent')} />
          
          {this.renderUpdateBtn()}
          {this.renderDeleteBtn()}
        </div>
      </div>
    )
  }
}

export default connect(
  mapStateToProps, 
  mapDispatchToProps
)(DiaryUpdate)