import { createReducers } from '../modules/redux-util'

let PasswordAddApp = createReducers({
  getInitialState () {
    return {
      status: '',
    }
  },
  addPassword (state, action) {
    state.status = ''
    return state
  },
  addPasswordPedding (state, action) {
    state.status = 'pending'
    return state
  },
  addPasswordSuccess (state, action) {
    state.status = 'success'
    return state
  },
  addPasswordError (state, action) {
    state.status = 'error'
    return state
  },
})

export default PasswordAddApp