import { createAction } from '../modules/redux-util'
import Immutable from 'immutable'
import Axios from '../modules/Axios'

export let initConsumptionPage = createAction('initConsumptionPage')
export let nextConsumptionPage = createAction('nextConsumptionPage')
export let setCurrentConsumption = createAction('setCurrentConsumption')

export let getConsumptionPedding = createAction('getConsumptionPedding')
export let getConsumptionSuccess = createAction('getConsumptionSuccess')
export let getConsumptionError = createAction('getConsumptionError')
export let getConsumption = createAction('getConsumption', (callback) => (dispatch, getState) => {
  dispatch( getConsumptionPedding() )

  let currentPage = getState().ConsumptionApp.currentPage
  let reqParams = {
    page: currentPage
  }

  Axios.get('/consumption/index', reqParams)
    .then((data)=>{
      if (data.Ret == '0') {
        dispatch( getConsumptionSuccess(data) )
        if (callback) callback(data)
      } else {
        dispatch( getConsumptionError() )
      }
    })
    .catch((error)=>{
      console.error(error)
      dispatch( getConsumptionError() )
    });
})