import { createAction } from '../modules/redux-util'
import Immutable from 'immutable'
import Axios from '../modules/Axios'

export let showToptips = createAction('showToptips')
export let hideToptips = createAction('hideToptips')

export let updateConsumptionPedding = createAction('updateConsumptionPedding')
export let updateConsumptionSuccess = createAction('updateConsumptionSuccess')
export let updateConsumptionError = createAction('updateConsumptionError')
export let updateConsumption = createAction('updateConsumption', (options, callback) => (dispatch, getState) => {
  dispatch( updateConsumptionPedding() )

  if (!options.sConsumptionItem) {
    dispatch( showToptips({msg: '项目未填'}) )
    dispatch( updateConsumptionError() )
    return
  }
  if (options.dMoney<=0) {
    dispatch( showToptips({msg: '金额错误'}) )
    dispatch( updateConsumptionError() )
    return
  }

  Axios.post('/consumption/update', options)
    .then((data)=>{
      if (data.Ret == '0') {
        dispatch( updateConsumptionSuccess(data) )
        if (callback) callback(data)
      } else {
        dispatch( updateConsumptionError() )
      }
    })
    .catch((error)=>{
      console.error(error)
      dispatch( updateConsumptionError() )
    });
})

export let deleteConsumptionPedding = createAction('deleteConsumptionPedding')
export let deleteConsumptionSuccess = createAction('deleteConsumptionSuccess')
export let deleteConsumptionError = createAction('deleteConsumptionError')
export let deleteConsumption = createAction('deleteConsumption', (options, callback) => (dispatch, getState) => {
  dispatch( deleteConsumptionPedding() )

  Axios.post('/consumption/delete', options)
    .then((data)=>{
      if (data.Ret == '0') {
        dispatch( deleteConsumptionSuccess(data) )
        if (callback) callback(data)
      } else {
        dispatch( deleteConsumptionError() )
      }
    })
    .catch((error)=>{
      console.error(error)
      dispatch( deleteConsumptionError() )
    });
})