import React, { Component } from 'react'
import { connect } from 'react-redux'
import * as Actions from '../../actions/note'
const mapStateToProps = state => state
const mapDispatchToProps = Actions
import classname from 'classname'

import TopTips from '../../components/TopTips/TopTips'
import InfiniteLoader from '../../components/InfiniteLoader/InfiniteLoader'
import Loading from '../../components/Loading/Loading'
import './Note.scss'

class Note extends Component {

  constructor(props) {
      super(props)

      this.state = {
      }

      this.renderList = this.renderList.bind(this)
      this.onRefresh = this.onRefresh.bind(this)
      this.onLoadMore = this.onLoadMore.bind(this)
      this.toAdd = this.toAdd.bind(this)
      this.toUpdate = this.toUpdate.bind(this)
      
      // 获取第一页数据
      this.onRefresh()
  }

  onRefresh (resolve, reject) {
    this.props.initNotePage()
    this.props.getNote(resolve)
  }
  onLoadMore (callback, finish) {
    if (this.props.NoteApp.currentPage >= this.props.NoteApp.pageCount) {
      finish()
    } else {
      this.props.nextNotePage()
      this.props.getNote(callback)
    }
  }

  renderList () {
    let list = this.props.NoteApp.list

    return (
      <ul>
        {list.map(item => {
          return (
            <li key={item.uNoteID} onClick={this.toUpdate.bind(null, item)}>
              <div className="note_title">{item.sNoteTitle}</div>
              <div className="note_content">{item.tNoteContent}</div>
            </li>
          )
        })}
      </ul>
    )
  }

  toAdd () {
    this.props.history.push( 'note_add' )
  }

  toUpdate (item) {
    this.props.setCurrentNote(item)
    this.props.history.push( 'note_update' )
  }

  render () {
    return (
      <div className="Note">
        <TopTips/>

        <InfiniteLoader 
          onLoadMore={this.onLoadMore}
          onRefresh={this.onRefresh}>
          
          <div className="add_note" onClick={this.toAdd}>+</div>
          {this.renderList()}
          
          <Loading show={this.props.NoteApp.status === 'pedding'}/>
        </InfiniteLoader>
      </div>
    )
  }
}

export default connect(
  mapStateToProps, 
  mapDispatchToProps
)(Note)