import { createReducers } from '../modules/redux-util'

let LoginApp = createReducers({
  getInitialState () {
    return {
      status: false,
    }
  },
  loginPending (state, action) {
    state.status = 'pending'
    return state
  },
  loginSuccess (state, action) {
    state.status = 'success'
    return state
  },
  loginError (state, action) {
    state.status = 'error'
    return state
  },
})

export default LoginApp

