import { createAction } from '../modules/redux-util'
import Axios from '../modules/Axios'
import Immutable from 'immutable'

// 登录
export let loginPending = createAction('loginPending')
export let loginSuccess = createAction('loginSuccess')
export let loginError = createAction('loginError')
export let loginAsync = createAction('login', (options, callback) => (dispatch, getState) => {
  dispatch( loginPending() )

  Axios.post('/user/login', options)
    .then((data)=>{
      if (data.Ret == '0') {
      	callback(data.Data)
      } else {
      	dispatch( loginError() )
      }
    })
	  .catch((error)=>{
	    console.error(error)
	    dispatch( loginError() )
	  });
})