import React from 'react'
import classname from 'classname'

import './Button.scss'

class Button extends React.Component {
  static defaultProps = {
      type: '',
      size: '',
      children: '按钮',
      disabled: false,
  }

  constructor(props){
    super(props)

    this.onClick = this.onClick.bind(this)
  }

  onClick () {
    if (this.props.disabled != 'disabled') {
      this.props.onClick()
    }
  }

  render () {
    let className = classname('btn', this.props.type, this.props.size, this.props.className)
    return (
      <div className={className} onClick={this.onClick} disabled={this.props.disabled}>
        {this.props.children}
      </div>
    )
  }
}

export default Button