import React, { Component } from 'react'
import { connect } from 'react-redux'
import * as Actions from '../../actions/diary'
const mapStateToProps = state => state
const mapDispatchToProps = Actions
import classname from 'classname'

import TopTips from '../../components/TopTips/TopTips'
import InfiniteLoader from '../../components/InfiniteLoader/InfiniteLoader'
import Loading from '../../components/Loading/Loading'
import './Diary.scss'

class Diary extends Component {

  constructor(props) {
      super(props)

      this.state = {
      }

      this.renderList = this.renderList.bind(this)
      this.onRefresh = this.onRefresh.bind(this)
      this.onLoadMore = this.onLoadMore.bind(this)
      this.toAdd = this.toAdd.bind(this)
      this.toUpdate = this.toUpdate.bind(this)
      
      // 获取第一页数据
      this.onRefresh()
  }

  onRefresh (resolve, reject) {
    this.props.initDiaryPage()
    this.props.getDiary(resolve)
  }
  onLoadMore (callback, finish) {
    if (this.props.DiaryApp.currentPage >= this.props.DiaryApp.pageCount) {
      finish()
    } else {
      this.props.nextDiaryPage()
      this.props.getDiary(callback)
    }
  }

  renderList () {
    let list = this.props.DiaryApp.list

    return (
      <ul>
        {list.map(item => {
          return (
            <li key={item.uDiaryID} onClick={this.toUpdate.bind(null, item)}>
              <div className="diary_date">{item.sDate}</div>
              <div className="diary_content">{item.tDiaryContent}</div>
            </li>
          )
        })}
      </ul>
    )
  }

  toAdd () {
    this.props.history.push( 'diary_add' )
  }

  toUpdate (item) {
    this.props.setCurrentDiary(item)
    this.props.history.push( 'diary_update' )
  }

  render () {
    return (
      <div className="Diary">
        <TopTips/>

        <InfiniteLoader 
          onLoadMore={this.onLoadMore}
          onRefresh={this.onRefresh}>
          
          <div className="add_diary" onClick={this.toAdd}>+</div>
          {this.renderList()}
          
          <Loading show={this.props.DiaryApp.status === 'pedding'}/>
        </InfiniteLoader>
      </div>
    )
  }
}

export default connect(
  mapStateToProps, 
  mapDispatchToProps
)(Diary)