import { createAction } from '../modules/redux-util'
import Immutable from 'immutable'
import Axios from '../modules/Axios'

export let showToptips = createAction('showToptips')
export let hideToptips = createAction('hideToptips')

export let addPasswordPedding = createAction('addPasswordPedding')
export let addPasswordSuccess = createAction('addPasswordSuccess')
export let addPasswordError = createAction('addPasswordError')
export let addPassword = createAction('addPassword', (options, callback) => (dispatch, getState) => {
  dispatch( addPasswordPedding() )

  if (!options.sPasswordItem) {
    dispatch( showToptips({msg: '密码项为空'}) )
    dispatch( addPasswordError() )
    return
  }
  if (!options.sUserName) {
    dispatch( showToptips({msg: '用户名为空'}) )
    dispatch( addPasswordError() )
    return
  }
  if (!options.sPassword) {
    dispatch( showToptips({msg: '密码为空'}) )
    dispatch( addPasswordError() )
    return
  }

  Axios.post('/password/add', options)
    .then((data)=>{
      if (data.Ret == '0') {
        dispatch( addPasswordSuccess(data) )
        if (callback) callback(data)
      } else {
        dispatch( addPasswordError() )
      }
    })
    .catch((error)=>{
      console.error(error)
      dispatch( addPasswordError() )
    });
})