import React from 'react';
import ReactDOM from 'react-dom'
import classNames from 'classname'

import './Infiniteloader.css';

/**
 *  上拉加载下一页&下拉刷新组件
 *
 */
class InfiniteLoader extends React.Component{
    static defaultProps = {
        triggerPercent: 75,
        onRefresh: (resolve, reject) => setTimeout( ()=> resolve(), 1000),
        disable: false
    }

    constructor(props){
        super(props);

        this.state = {
            loading: false,
            finish: false,
            scrollTimer: null,
            pullPercent: 0,
            touching: false,
            ogY: 0,
            touchId: undefined,
            animating: false,
            refreshing: false,
            initScrollTop: 0
        };

        this.finish = this.finish.bind(this);
        this.resolveLoading = this.resolveLoading.bind(this);
        this.scrollHandle = this.scrollHandle.bind(this);

        this.handleTouchStart = this.handleTouchStart.bind(this);
        this.handleTouchMove = this.handleTouchMove.bind(this);
        this.handleTouchEnd = this.handleTouchEnd.bind(this);
        this.resolveRefresh = this.resolveRefresh.bind(this);
    }

    finish(){
        this.setState({
            loading: false,
            finish: true
        });
    }

    resolveLoading(){
        this.setState({
            loading: false,
            finish: false
        });
    }

    scrollHandle(e){
        if (this.props.onScroll) this.props.onScroll(e);
        if (this.state.loading || this.state.finish || this.props.disable || e.target.scrollTop === 0) return;

        //setup for scrollend event
        clearTimeout(this.state.scrollTimer);
        this.setState({ scrollTimer: setTimeout( ()=>{
            if (this.props.onScrollEnd) this.props.onScrollEnd();
        }, 150) });

        let target = e.target;
        let scrollPercent = Math.floor(( (target.scrollTop + target.clientHeight) / target.scrollHeight) * 100);

        if (scrollPercent > this.props.triggerPercent) {
            this.setState({
                loading: true
            });

            this.props.onLoadMore(this.resolveLoading, this.finish);
        }
    }

    resolveRefresh(){
        this.setState({
            refreshing: false,
            animating: true,
            pullPercent: 0
        }, ()=>{
            setTimeout(()=>this.setState({ animating: false}), 500);
        });
    }

    handleTouchStart(e){
        if (this.state.touching || this.state.refreshing || this.props.disable) return;

        let $content = ReactDOM.findDOMNode(this.refs.content);
        
        this.setState({
            touching: true,
            touchId: e.targetTouches[0].identifier,
            ogY: this.state.pullPercent === 0 ? e.targetTouches[0].pageY : e.targetTouches[0].pageY - this.state.pullPercent,
            animating: false,
            initScrollTop: $content.scrollTop
        });
    }

    handleTouchMove(e){
        if (!this.state.touching || this.state.refreshing || this.props.disable) return;
        if (e.targetTouches[0].identifier !== this.state.touchId) return;


        const pageY = e.targetTouches[0].pageY;
        let diffY = pageY - this.state.ogY;

        //if it's scroll
        if (diffY < 0) return;

        //if it's not at top
        let $content = ReactDOM.findDOMNode(this.refs.content);
        if ($content.scrollTop > 0) return;

        //prevent move background
        e.preventDefault();

        diffY = ( diffY - this.state.initScrollTop ) > 100 ? 100 : ( diffY - this.state.initScrollTop );

        this.setState({
            pullPercent: diffY
        });
    }

    handleTouchEnd(e){
        if (!this.state.touching || this.state.refreshing || this.props.disable) return;

        let pullPercent = this.state.pullPercent;
        let refreshing = false;

        if (pullPercent === 100) {
            refreshing = true;
        }
        else {
            pullPercent = 0;
        }

        this.setState({
            touching: false,
            ogY: 0,
            touchId: undefined,
            initScrollTop: 0,
            animating: refreshing,
            pullPercent,
            refreshing
        }, ()=>{
            //triger after ui change
            if (refreshing) this.props.onRefresh(this.resolveRefresh);
        });
    }

    render(){
        const { children, className, triggerPercent, disable, onScrollEnd, onScroll, onLoadMore, onRefresh, ...domProps } = this.props;
        const clx = classNames( 'infiniteloader', className );

        let contentStyle = {
            overflow: disable ? 'hidden' : 'scroll',
            marginTop: `${(this.state.pullPercent)}px`,
            transition: this.state.animating ? 'all .3s' : 'none'
        };

        return (
            <div
              ref="content"
              className={clx}
              onScroll={this.scrollHandle}
              {...domProps}>

                <div
                  className="infiniteloader_content"
                  style={contentStyle}
                  ref="container"
                  onTouchStart={this.handleTouchStart}
                  onTouchMove={this.handleTouchMove}
                  onTouchEnd={this.handleTouchEnd}>

                  { children }
                </div>
            </div>
        );
    }
}

export default InfiniteLoader;
