import React, { Component } from 'react'
import { connect } from 'react-redux'
import * as Actions from '../../actions/login'
const mapStateToProps = state => state
const mapDispatchToProps = Actions

import classname from 'classname'
import Button from '../../components/Button/Button'
import './Login.scss'

class Login extends Component {

  constructor(props) {
    super(props)

    this.state = {
    }

    this.onLogin = this.onLogin.bind(this)
    this.onEnter = this.onEnter.bind(this)
  }

  onEnter (e) {
    if(e.keyCode===13){
      this.onLogin()
    }
  }

  onLogin () {
    let that = this
    let username = this.refs.username.value
    let password = this.refs.password.value
    let options = {
      username: username,
      password: password
    }
    this.props.loginAsync(options, (data)=>{
      localStorage.setItem('token', data.sAccessToken)
      that.props.history.push('/')
    })
  }

  render() {
    let loginClass = classname('login-panel', {'shake' :this.props.LoginApp.status=='error'})

    return (
      <div className="Login">
        <div className={loginClass}>
          <div className="login-input">
            <input ref="username" type="text"/>
          </div>
          <div className="login-input">
            <input ref="password" type="password" onKeyUp={this.onEnter}/>
          </div>
          <Button type='btn-add' onClick={this.onLogin}>
            登录
          </Button>
        </div>
      </div>
    )
  }
}

export default connect(
  mapStateToProps, 
  mapDispatchToProps
)(Login)