import { createAction } from '../modules/redux-util'
import Immutable from 'immutable'
import Axios from '../modules/Axios'

export let showToptips = createAction('showToptips')
export let hideToptips = createAction('hideToptips')

export let updatePasswordPedding = createAction('updatePasswordPedding')
export let updatePasswordSuccess = createAction('updatePasswordSuccess')
export let updatePasswordError = createAction('updatePasswordError')
export let updatePassword = createAction('updatePassword', (options, callback) => (dispatch, getState) => {
  dispatch( updatePasswordPedding() )

  if (!options.sPasswordItem) {
    dispatch( showToptips({msg: '密码项为空'}) )
    dispatch( updatePasswordError() )
    return
  }
  if (!options.sUserName) {
    dispatch( showToptips({msg: '用户名为空'}) )
    dispatch( updatePasswordError() )
    return
  }
  if (!options.sPassword) {
    dispatch( showToptips({msg: '密码为空'}) )
    dispatch( updatePasswordError() )
    return
  }

  Axios.post('/password/update', options)
    .then((data)=>{
      if (data.Ret == '0') {
        dispatch( updatePasswordSuccess(data) )
        if (callback) callback(data)
      } else {
        dispatch( updatePasswordError() )
      }
    })
    .catch((error)=>{
      console.error(error)
      dispatch( updatePasswordError() )
    });
})

export let deletePasswordPedding = createAction('deletePasswordPedding')
export let deletePasswordSuccess = createAction('deletePasswordSuccess')
export let deletePasswordError = createAction('deletePasswordError')
export let deletePassword = createAction('deletePassword', (options, callback) => (dispatch, getState) => {
  dispatch( deletePasswordPedding() )

  Axios.post('/password/delete', options)
    .then((data)=>{
      if (data.Ret == '0') {
        dispatch( deletePasswordSuccess(data) )
        if (callback) callback(data)
      } else {
        dispatch( deletePasswordError() )
      }
    })
    .catch((error)=>{
      console.error(error)
      dispatch( deletePasswordError() )
    });
})