import React from 'react'
import classname from 'classname'

import LinksConfig from './config'
import logo from '../../assets/img/aura.png'
import './SideBar.scss'

class SideBar extends React.Component {
  static defaultProps = {
      show: false,
  }

  constructor(props){
    super(props)

    this.renderLinks = this.renderLinks.bind(this)
    this.onClickNav = this.onClickNav.bind(this)
  }

  onClickNav (link) {
    this.props.history.push( link.path )
  }

  renderLinks () {
    let pathname = this.props.pathname

    return (
      <ul>
        {LinksConfig.map(link => {
          let className = ''
          if (link.active.some(url => pathname==url)) {
            className = 'sidebar_active'
          }
          return <li key={link.key} className={className} onClick={this.onClickNav.bind(this, link)}>{link.title}</li>
        })}
      </ul>
    )    
  }

  render () {
    let sidebarClassName = classname('sidebar', {'sidebar_show': this.props.show})
    let modalClassName = classname('sidebar_modal', {'sidebar_modal_show': this.props.show})

    return (
      <div>
        <div className={sidebarClassName} onClick={this.props.hideSidebar}>

          <div className="sidebar_logo">
            <img src={logo} className="logo_rotation" />
          </div>

          {this.renderLinks()}

        </div>
        
        <div className={modalClassName} onClick={this.props.hideSidebar}></div>
      </div>
    )
  }
}

export default SideBar