import { createReducers } from '../modules/redux-util'

let DiaryApp = createReducers({
  getInitialState () {
    return {
      status: '',
      list: [],
      pageCount: 1, //总页数
      currentPage: 1, //当前页数
    }
  },
  getDiary (state, action) {
    state.status = ''
    return state
  },
  getDiaryPedding (state, action) {
    state.status = 'pending'
    return state
  },
  getDiarySuccess (state, action) {
    state.status = 'success'
    state.list = state.list.concat(action.payload.Data)
    state.pageCount = action.payload.Meta.pageCount
    state.currentPage = action.payload.Meta.currentPage
    return state
  },
  getDiaryError (state, action) {
    state.status = 'error'
    return state
  },
  initDiaryPage (state, action) {
    state.list = []
    state.currentPage = 1
    return state
  },
  nextDiaryPage (state, action) {
    state.currentPage++
    return state
  },
})

export default DiaryApp