import React, { Component } from 'react'
import { connect } from 'react-redux'
import * as Actions from '../../actions/passwordAdd'
const mapStateToProps = state => state
const mapDispatchToProps = Actions
import classname from 'classname'

import Input from '../../components/Input/Input'
import Textarea from '../../components/Textarea/Textarea'
import Button from '../../components/Button/Button'
import TopTips from '../../components/TopTips/TopTips'
import './PasswordAdd.scss'

class PasswordAdd extends Component {

  constructor(props) {
      super(props)

      this.state = {
        sPasswordItem: '',
        sUserName: '',
        sPassword: '',
        tRemark: '',
      }

      this.onChange = this.onChange.bind(this)
      this.onSubmit = this.onSubmit.bind(this)
      this.renderBtn = this.renderBtn.bind(this)
  }

  onChange (key, e) {
    let val = e.target.value

    this.setState({
      [key]: val
    })
  }

  onSubmit () {
    this.props.addPassword({
      sPasswordItem: this.state.sPasswordItem,
      sUserName: this.state.sUserName,
      sPassword: this.state.sPassword,
      tRemark: this.state.tRemark,
    }, ()=>{
      // 回到列表
      this.props.showToptips({msg: '已新增'})
      this.props.history.push( 'password' )
    })
  }

  renderBtn () {
    if (this.props.PasswordAddApp.status === 'pending') {
      return (
        <Button type='btn-add' disabled="disabled">
          新增中...
        </Button>
      )
    } else {
      return (
        <Button type='btn-add' onClick={this.onSubmit}>
          新增
        </Button>
      )
    }
  }

  render () {
    let state = this.state

    return (
      <div className="Password_add">
        <TopTips/>

        <div className="password_form">
          <Input type="text" placeholder="项目" value={state.sPasswordItem} onChange={this.onChange.bind(this, 'sPasswordItem')} />
          <Input type="text" placeholder="用户名" value={state.sUserName} onChange={this.onChange.bind(this, 'sUserName')} />
          <Input type="text" placeholder="密码" value={state.sPassword} onChange={this.onChange.bind(this, 'sPassword')} />
          <Textarea value={state.tRemark} onChange={this.onChange.bind(this, 'tRemark')} />
          
          {this.renderBtn()}
        </div>
      </div>
    )
  }
}

export default connect(
  mapStateToProps, 
  mapDispatchToProps
)(PasswordAdd)