import { createReducers } from '../modules/redux-util'

let NoteApp = createReducers({
  getInitialState () {
    return {
      status: '',
      list: [],
      pageCount: 1, //总页数
      currentPage: 1, //当前页数
    }
  },
  getNote (state, action) {
    state.status = ''
    return state
  },
  getNotePedding (state, action) {
    state.status = 'pending'
    return state
  },
  getNoteSuccess (state, action) {
    state.status = 'success'
    state.list = state.list.concat(action.payload.Data)
    state.pageCount = action.payload.Meta.pageCount
    state.currentPage = action.payload.Meta.currentPage
    return state
  },
  getNoteError (state, action) {
    state.status = 'error'
    return state
  },
  initNotePage (state, action) {
    state.list = []
    state.currentPage = 1
    return state
  },
  nextNotePage (state, action) {
    state.currentPage++
    return state
  },
})

export default NoteApp