import React, { Component } from 'react'
import { connect } from 'react-redux'
import * as Actions from '../../actions/passwordUpdate'
const mapStateToProps = state => state
const mapDispatchToProps = Actions
import classname from 'classname'

import Input from '../../components/Input/Input'
import Textarea from '../../components/Textarea/Textarea'
import Button from '../../components/Button/Button'
import TopTips from '../../components/TopTips/TopTips'
import './PasswordUpdate.scss'

class PasswordUpdate extends Component {

  constructor(props) {
      super(props)
      this.state = {
        uPasswordID: this.props.PasswordUpdateApp.current.uPasswordID,
        sPasswordItem: this.props.PasswordUpdateApp.current.sPasswordItem,
        sUserName: this.props.PasswordUpdateApp.current.sUserName,
        sPassword: this.props.PasswordUpdateApp.current.sPassword,
        tRemark: this.props.PasswordUpdateApp.current.tRemark,
      }

      this.onChange = this.onChange.bind(this)
      this.onSubmit = this.onSubmit.bind(this)
      this.onDelete = this.onDelete.bind(this)
      this.renderUpdateBtn = this.renderUpdateBtn.bind(this)
      this.renderDeleteBtn = this.renderDeleteBtn.bind(this)
  }

  onChange (key, e) {
    let val = e.target.value

    this.setState({
      [key]: val
    })
  }

  onSubmit () {
    this.props.updatePassword({
      uPasswordID: this.state.uPasswordID,
      sPasswordItem: this.state.sPasswordItem,
      sUserName: this.state.sUserName,
      sPassword: this.state.sPassword,
      tRemark: this.state.tRemark,
    }, ()=>{
      // 回到列表
      this.props.showToptips({msg: '已修改'})
      this.props.history.push( 'password' )
    })
  }

  onDelete () {
    if( confirm("确认删除吗") ){
      this.props.deletePassword({
        uPasswordID: this.state.uPasswordID,
      }, ()=>{
        // 回到列表
        this.props.showToptips({msg: '已删除'})
        this.props.history.push( 'password' )
      })
    }
  }

  renderUpdateBtn () {
    if (this.props.PasswordUpdateApp.updateStatus === 'pending') {
      return (
        <Button type='btn-add' className="password_btn" disabled="disabled">
          修改中...
        </Button>
      )
    } else {
      return (
        <Button type='btn-add' className="password_btn" onClick={this.onSubmit}>
          修改
        </Button>
      )
    }
  }
  renderDeleteBtn () {
    if (this.props.PasswordUpdateApp.deleteStatus === 'pending') {
      return (
        <Button type='btn-delete' className="password_btn" disabled="disabled">
          删除中...
        </Button>
      )
    } else {
      return (
        <Button type='btn-delete' className="password_btn" onClick={this.onDelete}>
          删除
        </Button>
      )
    }
  }

  render () {
    let state = this.state

    return (
      <div className="Password_add">
        <TopTips/>

        <div className="password_form">
          <Input type="text" placeholder="项目" value={state.sPasswordItem} onChange={this.onChange.bind(this, 'sPasswordItem')} />
          <Input type="text" placeholder="用户名" value={state.sUserName} onChange={this.onChange.bind(this, 'sUserName')} />
          <Input type="text" placeholder="密码" value={state.sPassword} onChange={this.onChange.bind(this, 'sPassword')} />
          <Textarea value={state.tRemark} onChange={this.onChange.bind(this, 'tRemark')} />
          
          {this.renderUpdateBtn()}
          {this.renderDeleteBtn()}
        </div>
      </div>
    )
  }
}

export default connect(
  mapStateToProps, 
  mapDispatchToProps
)(PasswordUpdate)