import { createAction } from '../modules/redux-util'
import Immutable from 'immutable'
import Axios from '../modules/Axios'

export let showToptips = createAction('showToptips')
export let hideToptips = createAction('hideToptips')

export let addNotePedding = createAction('addNotePedding')
export let addNoteSuccess = createAction('addNoteSuccess')
export let addNoteError = createAction('addNoteError')
export let addNote = createAction('addNote', (options, callback) => (dispatch, getState) => {
  dispatch( addNotePedding() )

  if (!options.sNoteTitle) {
    dispatch( showToptips({msg: '标题为空'}) )
    dispatch( addNoteError() )
    return
  }
  if (!options.tNoteContent) {
    dispatch( showToptips({msg: '内容为空'}) )
    dispatch( addNoteError() )
    return
  }

  Axios.post('/note/add', options)
    .then((data)=>{
      if (data.Ret == '0') {
        dispatch( addNoteSuccess(data) )
        if (callback) callback(data)
      } else {
        dispatch( addNoteError() )
      }
    })
    .catch((error)=>{
      console.error(error)
      dispatch( addNoteError() )
    });
})