import { createReducers } from '../modules/redux-util'

let PasswordUpdateApp = createReducers({
  getInitialState () {
    return {
      updateStatus: '',
      deleteStatus: '',
      current: {
        uPasswordID: 0,
        sPasswordItem: '',
        sUserName: '',
        sPassword: '',
        tRemark: '',
      }
    }
  },
  setCurrentPassword (state, action) {
    state.current = action.payload
    return state
  }, 
  updatePassword (state, action) {
    state.updateStatus = ''
    return state
  },
  updatePasswordPedding (state, action) {
    state.updateStatus = 'pending'
    return state
  },
  updatePasswordSuccess (state, action) {
    state.updateStatus = 'success'
    return state
  },
  updatePasswordError (state, action) {
    state.updateStatus = 'error'
    return state
  },
  deletePassword (state, action) {
    state.deleteStatus = ''
    return state
  },
  deletePasswordPedding (state, action) {
    state.deleteStatus = 'pending'
    return state
  },
  deletePasswordSuccess (state, action) {
    state.deleteStatus = 'success'
    return state
  },
  deletePasswordError (state, action) {
    state.deleteStatus = 'error'
    return state
  },
})

export default PasswordUpdateApp