import { createAction } from '../modules/redux-util'
import Immutable from 'immutable'
import Axios from '../modules/Axios'

export let initPasswordPage = createAction('initPasswordPage')
export let nextPasswordPage = createAction('nextPasswordPage')
export let setCurrentPassword = createAction('setCurrentPassword')

export let getPasswordPedding = createAction('getPasswordPedding')
export let getPasswordSuccess = createAction('getPasswordSuccess')
export let getPasswordError = createAction('getPasswordError')
export let getPassword = createAction('getPassword', (callback) => (dispatch, getState) => {
  dispatch( getPasswordPedding() )

  let currentPage = getState().PasswordApp.currentPage
  let reqParams = {
    page: currentPage
  }

  Axios.get('/password/index', reqParams)
    .then((data)=>{
      if (data.Ret == '0') {
        dispatch( getPasswordSuccess(data) )
        if (callback) callback(data)
      } else {
        dispatch( getPasswordError() )
      }
    })
    .catch((error)=>{
      console.error(error)
      dispatch( getPasswordError() )
    });
})