import { createAction } from '../modules/redux-util'
import Immutable from 'immutable'
import Axios from '../modules/Axios'

export let showToptips = createAction('showToptips')
export let hideToptips = createAction('hideToptips')

export let addConsumptionPedding = createAction('addConsumptionPedding')
export let addConsumptionSuccess = createAction('addConsumptionSuccess')
export let addConsumptionError = createAction('addConsumptionError')
export let addConsumption = createAction('addConsumption', (options, callback) => (dispatch, getState) => {
  dispatch( addConsumptionPedding() )

  if (!options.sConsumptionItem) {
    dispatch( showToptips({msg: '项目未填'}) )
    dispatch( addConsumptionError() )
    return
  }
  if (options.dMoney<=0) {
    dispatch( showToptips({msg: '金额错误'}) )
    dispatch( addConsumptionError() )
    return
  }

  Axios.post('/consumption/add', options)
    .then((data)=>{
      if (data.Ret == '0') {
        dispatch( addConsumptionSuccess(data) )
        if (callback) callback(data)
      } else {
        dispatch( addConsumptionError() )
      }
    })
    .catch((error)=>{
      console.error(error)
      dispatch( addConsumptionError() )
    });
})