import { createReducers } from '../modules/redux-util'

let ConsumptionAddApp = createReducers({
  getInitialState () {
    return {
      status: '',
    }
  },
  addConsumption (state, action) {
    state.status = ''
    return state
  },
  addConsumptionPedding (state, action) {
    state.status = 'pending'
    return state
  },
  addConsumptionSuccess (state, action) {
    state.status = 'success'
    return state
  },
  addConsumptionError (state, action) {
    state.status = 'error'
    return state
  },
})

export default ConsumptionAddApp