import { createReducers } from '../modules/redux-util'

let ConsumptionApp = createReducers({
  getInitialState () {
    return {
      status: '',
      list: [],
      pageCount: 1, //总页数
      currentPage: 1, //当前页数
    }
  },
  getConsumption (state, action) {
    state.status = ''
    return state
  },
  getConsumptionPedding (state, action) {
    state.status = 'pending'
    return state
  },
  getConsumptionSuccess (state, action) {
    state.status = 'success'
    state.list = state.list.concat(action.payload.Data)
    state.pageCount = action.payload.Meta.pageCount
    state.currentPage = action.payload.Meta.currentPage
    return state
  },
  getConsumptionError (state, action) {
    state.status = 'error'
    return state
  },
  initConsumptionPage (state, action) {
    state.list = []
    state.currentPage = 1
    return state
  },
  nextConsumptionPage (state, action) {
    state.currentPage++
    return state
  },
})

export default ConsumptionApp