import React from 'react'
import { Route } from 'react-router'

import TitleConfig from '../components/TopBar/config'
import TopBar from '../components/TopBar/TopBar'
import '../assets/scss/main.scss'

import Home from './Home/Home'
import Login from './Login/Login'
import Consumption from './Consumption/Consumption'
import ConsumptionAdd from './ConsumptionAdd/ConsumptionAdd'
import ConsumptionUpdate from './ConsumptionUpdate/ConsumptionUpdate'
import Note from './Note/Note'
import NoteAdd from './NoteAdd/NoteAdd'
import NoteUpdate from './NoteUpdate/NoteUpdate'
import Diary from './Diary/Diary'
import DiaryAdd from './DiaryAdd/DiaryAdd'
import DiaryUpdate from './DiaryUpdate/DiaryUpdate'
import Password from './Password/Password'
import PasswordAdd from './PasswordAdd/PasswordAdd'
import PasswordUpdate from './PasswordUpdate/PasswordUpdate'
import Pedometer from './Pedometer/Pedometer'

class App extends React.Component {
  constructor(props){
    super(props)

    this.state = {} 
  }

  renderTopBar () {
    let pathname = this.props.history.location.pathname
    let title = TitleConfig[pathname]
    if (!title) {
      return null
    } else {
      return <TopBar title={title} pathname={pathname} {...this.props}/>
    }
  }

  render () {
    return (
      <div className="containers">         
        <Route path="/" component={Home} exact strict />
        <Route path="/login" component={Login} exact strict />
        <Route path="/consumption" component={Consumption} exact strict />
        <Route path="/consumption_add" component={ConsumptionAdd} exact strict />
        <Route path="/consumption_update" component={ConsumptionUpdate} exact strict />
        <Route path="/note" component={Note} exact strict />
        <Route path="/note_add" component={NoteAdd} exact strict />
        <Route path="/note_update" component={NoteUpdate} exact strict />
        <Route path="/diary" component={Diary} exact strict />
        <Route path="/diary_add" component={DiaryAdd} exact strict />
        <Route path="/diary_update" component={DiaryUpdate} exact strict />
        <Route path="/password" component={Password} exact strict />
        <Route path="/password_add" component={PasswordAdd} exact strict />
        <Route path="/password_update" component={PasswordUpdate} exact strict />
        <Route path="/pedometer" component={Pedometer} exact strict />

        {/* 顶栏和侧导航 */}
        {this.renderTopBar()}
      </div>
    )
  }

}

export default App