export default {
  /**
   * 返回格式化后的时间字符串
   * @param {Date} Date对象
   * @returns {String} 格式化后的时间, 如 2016-04-01
   */
  dateToString (dateObj) {
    let y = dateObj.getFullYear()
    let m = dateObj.getMonth()+1
    let d = dateObj.getDate()
    
    if (m<10) {
      m = '0' + m
    }
    if (d<10) {
      d = '0' + d
    }

    return `${y}-${m}-${d}`
  },
}
