import React, { Component } from 'react'
import { connect } from 'react-redux'
import * as Actions from '../../actions/ConsumptionUpdate'
const mapStateToProps = state => state
const mapDispatchToProps = Actions
import classname from 'classname'

import Time from '../../modules/Util/Time'
import Util from '../../modules/Util/Util'
import Input from '../../components/Input/Input'
import Select from '../../components/Select/Select'
import Button from '../../components/Button/Button'
import TopTips from '../../components/TopTips/TopTips'
import Category from '../../config/Dictionary/consumptionCategory'
import './ConsumptionUpdate.scss'

class ConsumptionUpdate extends Component {

  constructor(props) {
      super(props)

      this.state = {
        uConsumptionID: this.props.ConsumptionUpdateApp.current.uConsumptionID,
        sConsumptionItem: this.props.ConsumptionUpdateApp.current.sConsumptionItem,
        dMoney: this.props.ConsumptionUpdateApp.current.dMoney,
        sDate: this.props.ConsumptionUpdateApp.current.sDate,
        Category: this.props.ConsumptionUpdateApp.current.Category,
        sRemark: this.props.ConsumptionUpdateApp.current.sRemark,
      }

      this.onChange = this.onChange.bind(this)
      this.onSubmit = this.onSubmit.bind(this)
      this.onDelete = this.onDelete.bind(this)
      this.renderUpdateBtn = this.renderUpdateBtn.bind(this)
      this.renderDeleteBtn = this.renderDeleteBtn.bind(this)
  }

  onChange (key, e) {
    let val = e.target.value

    this.setState({
      [key]: val
    })
  }

  onSubmit () {
    this.props.updateConsumption({
      uConsumptionID: this.state.uConsumptionID,
      sConsumptionItem: this.state.sConsumptionItem,
      dMoney: this.state.dMoney,
      sDate: this.state.sDate,
      Category: this.state.Category,
      sRemark: this.state.sRemark,
    }, ()=>{
      // 回到列表
      this.props.showToptips({msg: '已修改'})
      this.props.history.push( 'consumption' )
    })
  }

  onDelete () {
    if( confirm("确认删除吗") ){
      this.props.deleteConsumption({
        uConsumptionID: this.state.uConsumptionID,
      }, ()=>{
        // 回到列表
        this.props.showToptips({msg: '已删除'})
        this.props.history.push( 'consumption' )
      })
    }
  }

  renderUpdateBtn () {
    if (this.props.ConsumptionUpdateApp.updateStatus === 'pending') {
      return (
        <Button type='btn-add' className="consumption_btn" disabled="disabled">
          修改中...
        </Button>
      )
    } else {
      return (
        <Button type='btn-add' className="consumption_btn" onClick={this.onSubmit}>
          修改
        </Button>
      )
    }
  }
  renderDeleteBtn () {
    if (this.props.ConsumptionUpdateApp.deleteStatus === 'pending') {
      return (
        <Button type='btn-delete' className="consumption_btn" disabled="disabled">
          删除中...
        </Button>
      )
    } else {
      return (
        <Button type='btn-delete' className="consumption_btn" onClick={this.onDelete}>
          删除
        </Button>
      )
    }
  }

  render () {
    let state = this.state
    let CategoryDate = Util.objToSelect(Category)

    return (
      <div className="Consumption_add">
        <TopTips/>

        <div className="consumption_form">
          <Input type="text" placeholder="项目" value={state.sConsumptionItem} onChange={this.onChange.bind(this, 'sConsumptionItem')} />
          <Input type="number" placeholder="金额" value={state.dMoney} onChange={this.onChange.bind(this, 'dMoney')} />
          <Input type="date" value={state.sDate} onChange={this.onChange.bind(this, 'sDate')} />
          <Select data={CategoryDate} defaultValue={state.Category} onChange={this.onChange.bind(this, 'Category')}/>
          <Input type="text" placeholder="备注" value={state.sRemark} onChange={this.onChange.bind(this, 'sRemark')} />
          
          {this.renderUpdateBtn()}
          {this.renderDeleteBtn()}
        </div>
      </div>
    )
  }
}

export default connect(
  mapStateToProps, 
  mapDispatchToProps
)(ConsumptionUpdate)