import { createReducers } from '../modules/redux-util'

let loginApp = createReducers({
  getInitialState () {
    return {
      show: false,
      msg: '',
      click: ()=>{}
    }
  },
  showToptips (state, action) {
    let click = action.payload.click
    state.show = true
    state.msg = action.payload.msg
    state.click = click ? click : ()=>{}
    return state
  },
  hideToptips (state, action) {
    state.show = false
    return state
  },
})

export default loginApp