import React, { Component } from 'react'
import { connect } from 'react-redux'
import * as Actions from '../../actions/noteUpdate'
const mapStateToProps = state => state
const mapDispatchToProps = Actions
import classname from 'classname'

import Input from '../../components/Input/Input'
import Textarea from '../../components/Textarea/Textarea'
import Button from '../../components/Button/Button'
import TopTips from '../../components/TopTips/TopTips'
import './NoteUpdate.scss'

class NoteUpdate extends Component {

  constructor(props) {
      super(props)
      this.state = {
        uNoteID: this.props.NoteUpdateApp.current.uNoteID,
        sNoteTitle: this.props.NoteUpdateApp.current.sNoteTitle,
        tNoteContent: this.props.NoteUpdateApp.current.tNoteContent,
      }

      this.onChange = this.onChange.bind(this)
      this.onSubmit = this.onSubmit.bind(this)
      this.onDelete = this.onDelete.bind(this)
      this.renderUpdateBtn = this.renderUpdateBtn.bind(this)
      this.renderDeleteBtn = this.renderDeleteBtn.bind(this)
  }

  onChange (key, e) {
    let val = e.target.value

    this.setState({
      [key]: val
    })
  }

  onSubmit () {
    this.props.updateNote({
      uNoteID: this.state.uNoteID,
      sNoteTitle: this.state.sNoteTitle,
      tNoteContent: this.state.tNoteContent,
    }, ()=>{
      // 回到列表
      this.props.showToptips({msg: '已修改'})
      this.props.history.push( 'note' )
    })
  }

  onDelete () {
    if( confirm("确认删除吗") ){
      this.props.deleteNote({
        uNoteID: this.state.uNoteID,
      }, ()=>{
        // 回到列表
        this.props.showToptips({msg: '已删除'})
        this.props.history.push( 'note' )
      })
    }
  }

  renderUpdateBtn () {
    if (this.props.NoteUpdateApp.updateStatus === 'pending') {
      return (
        <Button type='btn-add' className="note_btn" disabled="disabled">
          修改中...
        </Button>
      )
    } else {
      return (
        <Button type='btn-add' className="note_btn" onClick={this.onSubmit}>
          修改
        </Button>
      )
    }
  }
  renderDeleteBtn () {
    if (this.props.NoteUpdateApp.deleteStatus === 'pending') {
      return (
        <Button type='btn-delete' className="note_btn" disabled="disabled">
          删除中...
        </Button>
      )
    } else {
      return (
        <Button type='btn-delete' className="note_btn" onClick={this.onDelete}>
          删除
        </Button>
      )
    }
  }

  render () {
    let state = this.state

    return (
      <div className="Note_add">
        <TopTips/>

        <div className="note_form">
          <Input type="text" value={state.sNoteTitle} onChange={this.onChange.bind(this, 'sNoteTitle')} />
          <Textarea value={state.tNoteContent} onChange={this.onChange.bind(this, 'tNoteContent')} />
          
          {this.renderUpdateBtn()}
          {this.renderDeleteBtn()}
        </div>
      </div>
    )
  }
}

export default connect(
  mapStateToProps, 
  mapDispatchToProps
)(NoteUpdate)