import { createAction } from '../modules/redux-util'
import Immutable from 'immutable'
import Axios from '../modules/Axios'

export let initNotePage = createAction('initNotePage')
export let nextNotePage = createAction('nextNotePage')
export let setCurrentNote = createAction('setCurrentNote')

export let getNotePedding = createAction('getNotePedding')
export let getNoteSuccess = createAction('getNoteSuccess')
export let getNoteError = createAction('getNoteError')
export let getNote = createAction('getNote', (callback) => (dispatch, getState) => {
  dispatch( getNotePedding() )

  let currentPage = getState().NoteApp.currentPage
  let reqParams = {
    page: currentPage
  }

  Axios.get('/note/index', reqParams)
    .then((data)=>{
      if (data.Ret == '0') {
        dispatch( getNoteSuccess(data) )
        if (callback) callback(data)
      } else {
        dispatch( getNoteError() )
      }
    })
    .catch((error)=>{
      console.error(error)
      dispatch( getNoteError() )
    });
})