import { createAction } from '../modules/redux-util'
import Immutable from 'immutable'
import Axios from '../modules/Axios'

export let showToptips = createAction('showToptips')
export let hideToptips = createAction('hideToptips')

export let updateDiaryPedding = createAction('updateDiaryPedding')
export let updateDiarySuccess = createAction('updateDiarySuccess')
export let updateDiaryError = createAction('updateDiaryError')
export let updateDiary = createAction('updateDiary', (options, callback) => (dispatch, getState) => {
  dispatch( updateDiaryPedding() )

  if (!options.tDiaryContent) {
    dispatch( showToptips({msg: '内容为空'}) )
    dispatch( updateDiaryError() )
    return
  }

  Axios.post('/diary/update', options)
    .then((data)=>{
      if (data.Ret == '0') {
        dispatch( updateDiarySuccess(data) )
        if (callback) callback(data)
      } else {
        dispatch( updateDiaryError() )
      }
    })
    .catch((error)=>{
      console.error(error)
      dispatch( updateDiaryError() )
    });
})

export let deleteDiaryPedding = createAction('deleteDiaryPedding')
export let deleteDiarySuccess = createAction('deleteDiarySuccess')
export let deleteDiaryError = createAction('deleteDiaryError')
export let deleteDiary = createAction('deleteDiary', (options, callback) => (dispatch, getState) => {
  dispatch( deleteDiaryPedding() )

  Axios.post('/diary/delete', options)
    .then((data)=>{
      if (data.Ret == '0') {
        dispatch( deleteDiarySuccess(data) )
        if (callback) callback(data)
      } else {
        dispatch( deleteDiaryError() )
      }
    })
    .catch((error)=>{
      console.error(error)
      dispatch( deleteDiaryError() )
    });
})