import { createAction } from '../modules/redux-util'
import Immutable from 'immutable'
import Axios from '../modules/Axios'

export let showToptips = createAction('showToptips')
export let hideToptips = createAction('hideToptips')

export let updateNotePedding = createAction('updateNotePedding')
export let updateNoteSuccess = createAction('updateNoteSuccess')
export let updateNoteError = createAction('updateNoteError')
export let updateNote = createAction('updateNote', (options, callback) => (dispatch, getState) => {
  dispatch( updateNotePedding() )

  if (!options.sNoteTitle) {
    dispatch( showToptips({msg: '标题为空'}) )
    dispatch( updateNoteError() )
    return
  }
  if (!options.tNoteContent) {
    dispatch( showToptips({msg: '内容为空'}) )
    dispatch( updateNoteError() )
    return
  }

  Axios.post('/note/update', options)
    .then((data)=>{
      if (data.Ret == '0') {
        dispatch( updateNoteSuccess(data) )
        if (callback) callback(data)
      } else {
        dispatch( updateNoteError() )
      }
    })
    .catch((error)=>{
      console.error(error)
      dispatch( updateNoteError() )
    });
})

export let deleteNotePedding = createAction('deleteNotePedding')
export let deleteNoteSuccess = createAction('deleteNoteSuccess')
export let deleteNoteError = createAction('deleteNoteError')
export let deleteNote = createAction('deleteNote', (options, callback) => (dispatch, getState) => {
  dispatch( deleteNotePedding() )

  Axios.post('/note/delete', options)
    .then((data)=>{
      if (data.Ret == '0') {
        dispatch( deleteNoteSuccess(data) )
        if (callback) callback(data)
      } else {
        dispatch( deleteNoteError() )
      }
    })
    .catch((error)=>{
      console.error(error)
      dispatch( deleteNoteError() )
    });
})