import { createReducers } from '../modules/redux-util'

let PasswordApp = createReducers({
  getInitialState () {
    return {
      status: '',
      list: [],
      pageCount: 1, //总页数
      currentPage: 1, //当前页数
    }
  },
  getPassword (state, action) {
    state.status = ''
    return state
  },
  getPasswordPedding (state, action) {
    state.status = 'pending'
    return state
  },
  getPasswordSuccess (state, action) {
    state.status = 'success'
    state.list = state.list.concat(action.payload.Data)
    state.pageCount = action.payload.Meta.pageCount
    state.currentPage = action.payload.Meta.currentPage
    return state
  },
  getPasswordError (state, action) {
    state.status = 'error'
    return state
  },
  initPasswordPage (state, action) {
    state.list = []
    state.currentPage = 1
    return state
  },
  nextPasswordPage (state, action) {
    state.currentPage++
    return state
  },
})

export default PasswordApp