import { createReducers } from '../modules/redux-util'

let DiaryUpdateApp = createReducers({
  getInitialState () {
    return {
      updateStatus: '',
      deleteStatus: '',
      current: {
        uDiaryID: 0,
        sDate: '',
        tDiaryContent: '',
      }
    }
  },
  setCurrentDiary (state, action) {
    state.current = action.payload
    return state
  }, 
  updateDiary (state, action) {
    state.updateStatus = ''
    return state
  },
  updateDiaryPedding (state, action) {
    state.updateStatus = 'pending'
    return state
  },
  updateDiarySuccess (state, action) {
    state.updateStatus = 'success'
    return state
  },
  updateDiaryError (state, action) {
    state.updateStatus = 'error'
    return state
  },
  deleteDiary (state, action) {
    state.deleteStatus = ''
    return state
  },
  deleteDiaryPedding (state, action) {
    state.deleteStatus = 'pending'
    return state
  },
  deleteDiarySuccess (state, action) {
    state.deleteStatus = 'success'
    return state
  },
  deleteDiaryError (state, action) {
    state.deleteStatus = 'error'
    return state
  },
})

export default DiaryUpdateApp