import { createAction } from '../modules/redux-util'
import Immutable from 'immutable'
import Axios from '../modules/Axios'

export let showToptips = createAction('showToptips')
export let hideToptips = createAction('hideToptips')

export let addDiaryPedding = createAction('addDiaryPedding')
export let addDiarySuccess = createAction('addDiarySuccess')
export let addDiaryError = createAction('addDiaryError')
export let addDiary = createAction('addDiary', (options, callback) => (dispatch, getState) => {
  dispatch( addDiaryPedding() )

  if (!options.tDiaryContent) {
    dispatch( showToptips({msg: '内容为空'}) )
    dispatch( addDiaryError() )
    return
  }

  Axios.post('/diary/add', options)
    .then((data)=>{
      if (data.Ret == '0') {
        dispatch( addDiarySuccess(data) )
        if (callback) callback(data)
      } else {
        dispatch( addDiaryError() )
      }
    })
    .catch((error)=>{
      console.error(error)
      dispatch( addDiaryError() )
    });
})