import React, { Component } from 'react'
import { connect } from 'react-redux'
import * as Actions from '../../actions/toptips'
const mapStateToProps = state => state
const mapDispatchToProps = Actions
import classname from 'classname'

import './TopTips.css'

/**
 *  Drop down message from top
 *
 */

class TopTips extends Component {
  
  constructor(props){
    super(props)

    this.state = {
    }

    this.onClickToptips = this.onClickToptips.bind(this)
  }

  componentWillReceiveProps(nextProps){
    // 一段时间后自动关闭
    let that = this
    if(nextProps.TopTips.show){
      setTimeout(() => {
        that.props.hideToptips()
      }, 2000)
    }
  }

  onClickToptips(){
    this.props.hideToptips()
    this.props.TopTips.click()
  }

  render () {
    const cls = classname('toptips', {
      'toptips-show': this.props.TopTips.show
    })
    return (
      <div className={cls} onClick={this.onClickToptips}>
        {this.props.TopTips.msg}
      </div>
    )
  }
}

export default connect(
  mapStateToProps, 
  mapDispatchToProps
)(TopTips)
