import React, { Component } from 'react'
import { connect } from 'react-redux'
import * as Actions from '../../actions/password'
const mapStateToProps = state => state
const mapDispatchToProps = Actions
import classname from 'classname'

import TopTips from '../../components/TopTips/TopTips'
import InfiniteLoader from '../../components/InfiniteLoader/InfiniteLoader'
import Loading from '../../components/Loading/Loading'
import './Password.scss'

class Password extends Component {

  constructor(props) {
      super(props)

      this.state = {
      }

      this.renderList = this.renderList.bind(this)
      this.onRefresh = this.onRefresh.bind(this)
      this.onLoadMore = this.onLoadMore.bind(this)
      this.toAdd = this.toAdd.bind(this)
      this.toUpdate = this.toUpdate.bind(this)
      
      // 获取第一页数据
      this.onRefresh()
  }

  onRefresh (resolve, reject) {
    this.props.initPasswordPage()
    this.props.getPassword(resolve)
  }
  onLoadMore (callback, finish) {
    if (this.props.PasswordApp.currentPage >= this.props.PasswordApp.pageCount) {
      finish()
    } else {
      this.props.nextPasswordPage()
      this.props.getPassword(callback)
    }
  }

  renderList () {
    let list = this.props.PasswordApp.list

    return (
      <ul>
        {list.map(item => {
          return (
            <li key={item.uPasswordID} onClick={this.toUpdate.bind(null, item)}>
              <div className="password_title">{item.sPasswordItem}</div>
              <div className="password_username">{item.sUserName}</div>
            </li>
          )
        })}
      </ul>
    )
  }

  toAdd () {
    this.props.history.push( 'password_add' )
  }

  toUpdate (item) {
    this.props.setCurrentPassword(item)
    this.props.history.push( 'password_update' )
  }

  render () {
    return (
      <div className="Password">
        <TopTips/>

        <InfiniteLoader 
          onLoadMore={this.onLoadMore}
          onRefresh={this.onRefresh}>
          
          <div className="add_password" onClick={this.toAdd}>+</div>
          {this.renderList()}
          
          <Loading show={this.props.PasswordApp.status === 'pedding'}/>
        </InfiniteLoader>
      </div>
    )
  }
}

export default connect(
  mapStateToProps, 
  mapDispatchToProps
)(Password)