import React, { Component } from 'react'
import { connect } from 'react-redux'
import * as Actions from '../../actions/consumption'
const mapStateToProps = state => state
const mapDispatchToProps = Actions
import classname from 'classname'

import Category from '../../config/Dictionary/consumptionCategory'
import TopTips from '../../components/TopTips/TopTips'
import InfiniteLoader from '../../components/InfiniteLoader/InfiniteLoader'
import Loading from '../../components/Loading/Loading'
import './Consumption.scss'

class Consumption extends Component {

  constructor(props) {
      super(props)

      this.state = {
      }

      this.renderList = this.renderList.bind(this)
      this.onRefresh = this.onRefresh.bind(this)
      this.onLoadMore = this.onLoadMore.bind(this)
      this.toAdd = this.toAdd.bind(this)
      this.toUpdate = this.toUpdate.bind(this)
      
      // 获取第一页数据
      this.onRefresh()
  }

  onRefresh (resolve, reject) {
    this.props.initConsumptionPage()
    this.props.getConsumption(resolve)
  }
  onLoadMore (callback, finish) {
    if (this.props.ConsumptionApp.currentPage >= this.props.ConsumptionApp.pageCount) {
      finish()
    } else {
      this.props.nextConsumptionPage()
      this.props.getConsumption(callback)
    }
  }

  renderList () {
    let list = this.props.ConsumptionApp.list

    return (
      <ul>
        {list.map(item => {
          let categoryClass = classname('consumption_money', item.Category)
          return (
            <li key={item.uConsumptionID} onClick={this.toUpdate.bind(null, item)}>
              <div className="consumption_item">{item.sConsumptionItem}</div>
              <div className="consumption_date_category">{Category[item.Category]}   {item.sDate}</div>
              <div className="consumption_remark">{item.sRemark}</div>

              <div className={categoryClass}>{item.dMoney}</div>
            </li>
          )
        })}
      </ul>
    )
  }

  toAdd () {
    this.props.history.push( 'consumption_add' )
  }

  toUpdate (item) {
    this.props.setCurrentConsumption(item)
    this.props.history.push( 'consumption_update' )
  }

  render () {
    return (
      <div className="Consumption">
        <TopTips/>

        <InfiniteLoader 
          onLoadMore={this.onLoadMore}
          onRefresh={this.onRefresh}>
          
          <div className="add_consumption" onClick={this.toAdd}>+</div>
          {this.renderList()}
          
          <Loading show={this.props.ConsumptionApp.status === 'pedding'}/>
        </InfiniteLoader>
      </div>
    )
  }
}

export default connect(
  mapStateToProps, 
  mapDispatchToProps
)(Consumption)